<template>
	<header>
		<div class="ground_bg"></div>
		<nav id="navgation">
			<ul class="navgationlist">
				<li v-for='(item,index) in navgationlist'>
					<a v-bind:href="item.ChannelUrl">{{item.ChannelName}}</a>
				</li>
			</ul>
		</nav>
		<div id="blogname"><img src="http://img.52z.com/upload/news/image/20180623/20180623032709_55049.jpg" alt="图片丢失了"><a href="#">{{blogname}}</a></div>
	</header>
</template>
<script>
	export default {
		name :'blogheader',
		data (){
			return{
				blogname:'尹子个人博客',
				navgationlist:""
			}
		},
		created (){
			this.$axios("GetChannel",{
				params:{
					ChannelID: 345
				}
			}).then(res=>{
				this.navgationlist = res.data.Data;
			}).catch(error=>{
				console.log(error);
			})
		}
	};
</script>
<style scoped>
	header{ width: 100%; height: 400px; background: rgba(0,0,0,.4); }
	.ground_bg{ background: url(https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1555933553406&di=0301c8c36cd81a0b40a71426334a4e5a&imgtype=0&src=http%3A%2F%2Fi0.hdslb.com%2Fbfs%2Farticle%2Fd2c216c4b538b175fdd3a24bd1a9c479decfd1f3.jpg) top center no-repeat;
		-moz-background-size: cover; -o-background-size: cover; -webkit-background-size: cover; background-size: cover;
		top: 0; left: 0; position: fixed; width: 100%; height: 100%; z-index: -999; }

	nav{ height: 150px; }
	.navgationlist{ display: none; padding: 20px 0; width: 100%; }
	.navgationlist li a{ display: block; width: 100px; text-align: center; }

	#blogname{ width: 200px; margin: 0 auto; text-align: center; }
	#blogname img{ width: 100px; border-radius: 50%; }
	#blogname a{ display: block; margin-top: 20px; text-align: center; font-size: 18px; color: #FFF; font-weight: bold; text-shadow: 0 2px 4px rgba(0,0,0,.1); }
</style>


